body {
    background-color: #efe9e7; }
.wrap {
    max-width: 800px;
    margin: 20px auto 0 auto;
    @include phone() {
        margin: 0 auto; } }
.heading.slate {
    position: relative;
    display: block; }
#mapBtn,#listBtn {
    position: absolute;
    right: 28px;
    top: 28px; }
.item {
    display: flex;
    justify-content: space-between;
    text-align: center;
    .time {
        flex: 1 20px;
        margin: 0 5px; }
    .area {
        flex: 1 20px;
        margin: 0 5px;
        @include phone() {
            display: none; } }
    .location {
        flex: 3 20px;
        @include text-justify;
        margin: 0 5px; }
    .detail {
        flex: 3 20px;
        @include text-justify;
        margin: 0 5px; } }

#map {
    width: 100%;
    height: 500px; }
